.Header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  max-width: 1400px;
  height: 7rem;
  line-height: 7rem;
  line-height: 7rem;
  background: linear-gradient(to bottom, #cb6221 0%, #da9332 100%); }
  .Header .Logo {
    position: absolute;
    height: 7rem;
    line-height: 7rem;
    z-index: 100; }
    .Header .Logo a {
      display: grid;
      width: auto;
      height: 7rem;
      align-content: center; }
    .Header .Logo img {
      width: auto;
      height: 5rem; }
  .Header .Panel-btn {
    display: block;
    position: relative;
    z-index: 1000;
    left: calc(100% - 2.5rem); }
  .Header .Panel {
    position: fixed;
    z-index: 900;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: calc(100% + 5rem);
    overflow-y: auto;
    transform: translate(100%, 0);
    transition: transform .5s ease; }
    .Header .Panel.is-active {
      transform: translate(0, 0); }

.hamburger {
  padding: 0; }
  .hamburger:hover {
    opacity: 1; }
  .hamburger:focus {
    outline: 0;
    border: 0; }

.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
  background-color: #ffffff; }

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
  background-color: #ffffff; }

.Menu {
  margin: 0 auto;
  padding-top: 7rem;
  text-align: center;
  height: calc(100% - 5rem);
  background: linear-gradient(to bottom, #cb6221 0%, #da9332 100%); }
  .Menu ul {
    display: flex;
    flex-direction: column;
    list-style: none; }
  .Menu li {
    height: 4rem; }
  .Menu a {
    color: #ffffff;
    text-decoration: none;
    font-size: 2rem;
    text-transform: uppercase; }

